  animation-duration: 2s;
}
.hero-bg .hero-container .hero-content .animation .astronaut-movement {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  animation-name: astronautRotatePivotObject;
  animation-timing-function: cubic-bezier(1, 0, 0.95, 0.34);
  animation-fill-mode: forwards;
  animation-duration: 2s;
}
.hero-bg .hero-container .hero-content .animation .astronaut {
  width: 50px;
  height: 50px;
  -o-object-fit: cover;
     object-fit: cover;
  display: block;
  animation-name: spaceMovement;
  animation-duration: 5s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}
