  animation-duration: 0.5s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}
@keyframes rotateMovement {
  0% {
    opacity: 1;
    transform: rotate(0);
  }
  100% {
    opacity: 0.7;
    transform: rotate(-360deg);
  }
}
@keyframes starAnimation {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes astronautRotate {
  0% {
    transform: rotate(0deg) scale(0) translateX(0);
  }
  100% {
    transform: rotate(270deg) scale(1) translateX(-30%);
  }
}
@keyframes astronautRotatePivot {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(-270deg);
  }
}
@keyframes astronautRotatePivotObject {
  0% {
    opacity: 0;
