// FONTS

@font-face {
    font-family: 'Tusker Grotesk';
    src: url('./assets/fonts/TuskerGrotesk-9800Super.ttf');
    font-weight: bold;
}

@font-face {
    font-family: 'Tusker Grotesk';
    src: url('./assets/fonts/TuskerGrotesk-4500Medium.otf');
    font-weight: normal;
}

@font-face {
    font-family: 'Neue Montreal';
    src: url('./assets//fonts/NeueMontreal-Medium.otf');
    font-weight: normal;
}

// GLOBAL VARIABLES 

$primary-color: #6cb1dc;
$secondary-color: #d7bbf5;

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Neue Montreal', sans-serif;
    font-weight: normal;
    scroll-behavior: smooth;
    color-scheme: dark;

}

a {
    background: linear-gradient(to right, $primary-color, $secondary-color);
    background-clip: text;
    color: transparent;
    font-size: 1rem;
    text-decoration: none;
}

h1 {
    font-size: 3rem;
    letter-spacing: 8px;
    font-family: 'Tusker Grotesk', sans-serif;
    font-weight: bold;
}

h2 {
    background: linear-gradient(to right, $primary-color, $secondary-color);
    background-clip: text;
    color: transparent;
    font-size: 2rem;
    text-align: center;
    letter-spacing: 5px;
    font-family: 'Tusker Grotesk', sans-serif;
    font-weight: normal;
}

p {
    font-family: 'Neue Montreal', sans-serif;
    font-weight: normal;
    font-size: 1.25rem;
    
    line-height: 36px;
}

ul {
    list-style: none;
    padding-left: 0;
}

li {
    margin-bottom: 10px;
}

