// NavBar

.nav-container {
    width: 100%;
    display: flex;
    align-items: center;
    padding: 1.25rem 1.5rem;
    position: fixed;
    top: 0;
    z-index: 20;

    backdrop-filter: blur(0);
    /* Start with no blur */
    transition: backdrop-filter 0.8s ease;
    /* Add smooth transition effect */

    .navbar {
        width: 100%;
        display: flex;
        align-items: center;
        max-width: 1200px;
        margin: 0 auto;

        .column {
            width: calc(100% / 3);
            display: flex;
        }

        .nav-list {
            display: flex;
            justify-content: start;
            list-style-type: none;

            li {
                padding: 5px 10px;
            }
        }

        .nav-logo {
            justify-content: center;

            a {
                padding: 4px 8px;
            }
        }

        .nav-contact {
            justify-content: end;

            a {
                border: 1px solid black;
                padding: 5px;
            }
        }
    }
}

.nav-container.scroll {
    // background-color: rgba(42, 108, 173, 0.5);
    backdrop-filter: blur(4px) brightness(0.95);
    /* Apply the blur on scroll */
}

.hero-bg {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;

    .stars-container {
        width: 100%;
        height: auto;
        position: absolute;
        inset: 0;
        z-index: -1;
    }

    .hero-container {
        width: 100%;
        height: 100vh;
        position: relative;
        overflow: hidden hidden;
        display: flex;
        justify-content: center;
        align-items: center;

        .hero-content {
            // border: 1px solid grey;
            position: relative;
            width: 1200px;
            height: 450px;
            overflow: unset;

            @keyframes lightSpeedInRight {
                from {
                    transform: translate3d(100%, 0, 0) skewX(-30deg);
                    opacity: 0;
                }

                60% {
                    transform: skewX(20deg);
                    opacity: 1;
                }

                80% {
                    transform: skewX(-5deg);
                }

                to {
                    transform: translate3d(0, 0, 0);
                }
            }

            .lightSpeedInRight {
                animation-name: lightSpeedInRight;
                animation-timing-function: ease-out;
            }

            h1 {
                background: linear-gradient(to right,
                        $primary-color,
                        $secondary-color );
                background-clip: text;
                color: transparent;
                padding: 30px;
                animation-name: lightSpeedInRight;
                animation-timing-function: ease-out;
                margin-bottom: 70px;
            }

            p {
                padding-left: 60px;
                max-width: 700px;
                text-align: justify;
                letter-spacing: 4px;
            }

            @mixin anim {
                animation-timing-function: cubic-bezier(1, 0, 0.95, 0.34);
                animation-fill-mode: forwards;
                animation-duration: 2s;
            }

            .animation {
                width: 750px;
                height: 750px;
                position: absolute;
                bottom: 10px;
                right: -470px;

                animation-name: astronautRotate;
                @include anim();

                .astronaut-container {
                    position: absolute;
                    top: 0%;
                    left: 50%;
                    width: 0px;
                    height: 0px;
                    animation-name: astronautRotatePivot;
                    @include anim();
                }

                .astronaut-movement {
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    animation-name: astronautRotatePivotObject;
                    @include anim();
                }

                .astronaut {
                    width: 50px;
                    height: 50px;
                    object-fit: cover;
                    display: block;


                    animation-name: spaceMovement;
                    animation-duration: 5s;
                    animation-timing-function: linear;
                    animation-iteration-count: infinite;
                }

                .star {
                    width: 40px;
                    height: 40px;
                    object-fit: cover;
                    display: block;

                    animation-name: rotateMovement;
                    animation-duration: 0.5s;
                    animation-timing-function: linear;
                    animation-iteration-count: infinite;
                }
            }
        }

        $roateSpeed: 270deg;

        @keyframes rotateMovement {
            0% {
                opacity: 1;
                transform: rotate(0);
            }

            100% {
                opacity: 0.7;
                transform: rotate(-360deg);
            }
        }

        @keyframes starAnimation {
            0% {
                opacity: 1;
            }

            100% {
                opacity: 0;
            }
        }

        @keyframes astronautRotate {
            0% {
                transform: rotate(0deg) scale(0) translateX(0);
            }

            100% {
                transform: rotate($roateSpeed) scale(1) translateX(-30%);
            }
        }

        @keyframes astronautRotatePivot {
            0% {
                transform: rotate(0deg);
            }

            100% {
                transform: rotate(-$roateSpeed);
            }
        }

        @keyframes astronautRotatePivotObject {
            0% {
                opacity: 0;
                transform: translate(-50%, -50%) scale(1);
            }

            100% {
                opacity: 1;
                transform: translate(-50%, -50%) scale(10);
            }
        }

        @keyframes hoverboardAnimation {
            0% {
                transform: translateX(100px) translateY(0);
            }

            100% {
                transform: translateX(30px) translateY(-87px);
            }
        }

        @keyframes spaceMovement {
            0% {
                transform: translate(1%, -3%);
            }

            50% {
                transform: translate(-1%, 3%);
            }

            100% {
                transform: translate(1%, -3%);
            }
        }

        .scroll-icon-container {
            position: absolute;
            bottom: 40px;
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            z-index: 0;

            .scroll-icon {
                width: 35px;
                height: 64px;
                border-radius: 1.5rem;
                border: 4px solid $secondary-color;
                display: flex;
                justify-content: center;
                align-items: flex-start;
                padding: 0.5rem;

                .circle-icon {
                    width: 12px;
                    height: 12px;
                    border-radius: 50%;
                    background-color: $secondary-color;
                    margin-bottom: 0.25rem;
                }
            }
        }
    }



}

