// Timeline

::selection {
    color: #fff;
    background: #ff7979;
}

.wrapper {
    max-width: 1080px;
    margin: 50px auto;
    padding: 0 20px;
    position: relative;

    .center-line {
        position: absolute;
        height: 80%;
        width: 4px;
        background: #fff;
        left: 50%;
        top: 20px;
        transform: translateX(-50%);
    }

    .row {
        display: flex;

        &.row-1 {
            justify-content: flex-start;
        }

        &.row-2 {
            justify-content: flex-end;
        }

        section {

            color: black;
            background: #fff;
            border-radius: 5px;
            width: calc(50% - 40px);
            padding: 20px;
            position: relative;

            &::before {
                position: absolute;
                content: "";
                height: 15px;
                width: 15px;
                background: #fff;
                top: 28px;
                z-index: -1;
                transform: rotate(45deg);
            }
        }

        &.row-1 section::before {
            right: -7px;
        }

        &.row-2 section::before {
            left: -7px;
        }


        section .icon,
        .center-line .scroll-icon {
            position: absolute;
            background: #f2f2f2;
            height: 40px;
            width: 40px;
            text-align: center;
            line-height: 40px;
            border-radius: 50%;
            color: #483e84;
            font-size: 17px;
            box-shadow: 0 0 0 4px #fff, inset 0 2px 0 rgba(0, 0, 0, 0.08), 0 3px 0 4px rgba(0, 0, 0, 0.05);
        }

        .center-line .scroll-icon {
            bottom: 0px;
            left: 50%;
            font-size: 25px;
            transform: translateX(-50%);
        }

        &.row-1 section .icon {
            top: 15px;
            right: -60px;
        }

        &.row-2 section .icon {
            top: 15px;
            left: -60px;
        }

        section .details,
        section .bottom {

            display: flex;
            align-items: center;
            justify-content: space-between;
        }

        section .details .title {
            font-family: 'Neue Montreal', sans-serif;
            font-weight: normal;
            font-size: 22px;
            // font-weight: 600;
        }

        p {
            font-family: 'Neue Montreal', sans-serif;
            font-weight: normal;
            margin: 10px 0 17px 0;
        }

        .bottom a {
            text-decoration: none;
            background: #ff7979;
            color: #fff;
            padding: 7px 15px;
            border-radius: 5px;
            font-weight: 400;
            transition: all 0.3s ease;

            &:hover {
                transform: scale(0.97);
            }
        }
    }

    @media (max-width: 790px) {
        .center-line {
            left: 40px;
        }

        .row {
            margin: 30px 0 3px 60px;
        }

        section {
            width: 100%;
        }

        &.row-1 section::before {
            left: -7px;
        }

        &.row-1 section .icon {
            left: -60px;
        }
    }

    @media (max-width: 440px) {

        .center-line,
        section::before,
        section .icon {
            display: none;
        }

        .row {
            margin: 10px 0;
        }
    }
}